import React, {Component} from 'react';
import Particles from 'react-particles-js';


export default class ParticlesContainer extends Component {
render() {
    return (
        <Particles
    height='80vh'
    params={{
	    "particles": {
	        "number": {
	            "value": 150
	        },
	        "size": {
	            "value": 3
	        }
	    },
	    "interactivity": {
	        "events": {
	            "onhover": {
	                "enable": true,
	                "mode": "repulse"
	            }
	        }
	    }
	}}
                style={{
                        boxSize: 'border-box',
                        width: '100%',
                        position: 'absolute',
                        zIndex: '3',
                        marginTop: '60px',
                        marginBottom: '160px'
                 }} />
    )
}
}
